import M from '@materializecss/materialize';
const ClipboardJS = require('clipboard');
window.$ = require('jquery');


$(document).ready(function () {
    console.info("DOM LOADED!");
    M.AutoInit();
    unleash();
    let cbjs = new ClipboardJS('.btn-copy');
});

function makePeerId(length) {
    let result = '';
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789`-=~!@#$%^&*()_+,./<>?;:[]{}\\|';
    const charactersLength = characters.length;
    let counter = 0;
    while (counter < length) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
        counter += 1;
    }
    return result;
}

function unleash() {
    $(document).on('click', 'button#submit', function (e) {
        e.preventDefault();
        let hosts = [
            "reactor.filelist.io",
            "reactor.flro.org",
            "tracker.animebytes.tv",
            "routing.bgp.technology",
            "ssl.empirehost.me",
            "localhost.stackoverflow.tech",
            "tracker.hdtime.org",
            ".*",
            "*.*",
            "*.",
        ];
        let spoof = parseInt($('#form-select-1').val());
        let code = "";
        let random = makePeerId(12);
        switch (spoof) {
            case 1:
                hosts.forEach(function (value) {
                    code += "header:%0D%0AUser-Agent: Transmission/2.11\n" +
                        "peerid:-TR2110-"+random +"\n" +
                        "xfer_peerid:-TR2110-"+random+"\n\n";
                });
                $('button#submit').prop("disabled", true);
                $("#generated").text(code);
                $("#generated_container").slideDown('slow');
                break;
            case 2:
                break;
            case 3:
                break;
            case 4:
                break;
            default:
                break;
        }
    });
}